 .backdrop {
     margin-top: 20px;
     // position: fixed;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
     background-color: rgba(0, 0, 0, 0.2);

     transition: visibility 250ms cubic-bezier(0.4, 0, 0.2, 1),
         opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

     &.is-hidden {
         visibility: hidden;
         opacity: 0;
         pointer-events: none;
     }

     overflow-y: scroll;
 }

 .close-modal_button {
     position: absolute;
     top: 8px;
     right: 8px;
     padding: 0;
     display: flex;
     justify-content: center;
     align-items: center;
     width: 16px;
     height: 16px;
     background-color: inherit;
     border: none;
     cursor: pointer;

     @media screen and (min-width:768px) {
         width: 20px;
         height: 20px;
     }
 }

 .close-modal_icon {
     width: 9px;
     height: 9px;
     fill: currentColor;

     transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

     &:hover,
     &:focus {
         color: var(--accent-color);
     }

     @media screen and (min-width:768px) {
         width: 11px;
         height: 11px;
     }
 }

